<template>
  <div class="app-container">
    <el-form
      :model="queryParams"
      ref="queryForm"
      size="small"
      :inline="true"
      v-show="showSearch"
      label-width="100px"
    >
      <el-form-item label="订单编号" prop="orderSn">
        <el-input
          v-model="queryParams.orderSn"
          placeholder="请输入订单编号"
          clearable
        />
      </el-form-item>
      <el-form-item label="下单人ID" prop="customerId">
        <el-input
          v-model="queryParams.customerId"
          placeholder="请输入下单人ID"
          clearable
        />
      </el-form-item>
      <el-form-item label="收货人姓名" prop="shippingUser">
        <el-input
          v-model="queryParams.shippingUser"
          placeholder="请输入收货人姓名"
          clearable
        />
      </el-form-item>
      <el-form-item label="地址" prop="address">
        <el-input
          v-model="queryParams.address"
          placeholder="请输入地址"
          clearable
        />
      </el-form-item>
      <el-form-item label="快递公司名称" prop="shippingCompName">
        <el-input
          v-model="queryParams.shippingCompName"
          placeholder="请输入快递公司名称"
          clearable
        />
      </el-form-item>
      <el-form-item label="快递单号" prop="shippingSn">
        <el-input
          v-model="queryParams.shippingSn"
          placeholder="请输入快递单号"
          clearable
        />
      </el-form-item>
      <el-form-item label="发票抬头" prop="invoiceTime">
        <el-input
          v-model="queryParams.invoiceTime"
          placeholder="请输入发票抬头"
          clearable
        />
      </el-form-item>
      <el-form-item>
        <el-button
          type="primary"
          icon="el-icon-search"
          size="mini"
          @click="handleQuery"
          >搜索</el-button
        >
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery"
          >重置</el-button
        >
      </el-form-item>
    </el-form>
    <el-table
      v-loading="loading"
      :data="masterList"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55" align="center" />
      <el-table-column label="订单ID" align="center" prop="orderId" />
      <el-table-column label="订单编号" align="center" prop="orderSn" />
      <el-table-column label="下单人ID" align="center" prop="customerId" />
      <el-table-column label="收货人姓名" align="center" prop="shippingUser" />
      <el-table-column label="省" align="center" prop="province" />
      <el-table-column label="市" align="center" prop="city" />
      <el-table-column label="区" align="center" prop="district" />
      <el-table-column label="地址" align="center" prop="address" />
      <el-table-column label="订单金额" align="center" prop="orderMoney" />
      <el-table-column label="优惠金额" align="center" prop="districtMoney" />
      <el-table-column label="运费金额" align="center" prop="shippingMoney" />
      <el-table-column label="支付金额" align="center" prop="paymentMoney" />
      <el-table-column
        label="快递公司名称"
        align="center"
        prop="shippingCompName"
      />
      <el-table-column label="快递单号" align="center" prop="shippingSn" />
      <el-table-column
        label="发货时间"
        align="center"
        prop="shippingTime"
        width="180"
      >
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.shippingTime, "{y}-{m}-{d}") }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="支付时间"
        align="center"
        prop="payTime"
        width="180"
      >
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.payTime, "{y}-{m}-{d}") }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="收货时间"
        align="center"
        prop="receiveTime"
        width="180"
      >
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.receiveTime, "{y}-{m}-{d}") }}</span>
        </template>
      </el-table-column>
      <el-table-column label="订单状态" align="center" prop="orderStatus">
        <template slot-scope="scope">
          <dict-tag :options="orderStatusDict" :value="scope.row.orderStatus" />
        </template>
      </el-table-column>
      <el-table-column label="发票抬头" align="center" prop="invoiceTime" />
      <el-table-column
        label="操作"
        align="center"
        class-name="small-padding fixed-width"
      >
        <template slot-scope="scope">
          <el-button
            v-if="scope.row.orderStatus===0"
            size="mini"
            type="text"
            icon="el-icon-edit"
            @click="handleUpdate(scope.row)"
            >发货</el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <pagination
      v-show="total > 0"
      :total="total"
      :page.sync="queryParams.pageNum"
      :limit.sync="queryParams.pageSize"
      @pagination="getList"
    />

  </div>
</template>

<script>
import {
  listMaster,
  delMaster
} from '@/api/system/master'

export default {
  name: 'Master',
  data () {
    return {
      // 订单状态
      orderStatusDict: [
        {
          value: '0',
          label: '待发货',
          raw: {
            listClass: 'primary',
            isDefault: 'Y',
            default: true
          }
        },
        {
          value: '1',
          label: '待收货',
          raw: {
            listClass: 'primary',
            isDefault: 'Y',
            default: true
          }
        },
        {
          value: '2',
          label: '已收货',
          raw: {
            listClass: 'primary',
            isDefault: 'Y',
            default: true
          }
        }
      ],
      // 遮罩层
      loading: true,
      // 选中数组
      ids: [],
      // 非单个禁用
      single: true,
      // 非多个禁用
      multiple: true,
      // 显示搜索条件
      showSearch: true,
      // 总条数
      total: 0,
      // 订单主表格数据
      masterList: [],
      // 弹出层标题
      title: '',
      // 是否显示弹出层
      open: false,
      // 查询参数
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        orderSn: null,
        customerId: null,
        shippingUser: null,
        province: null,
        city: null,
        district: null,
        address: null,
        orderMoney: null,
        districtMoney: null,
        shippingMoney: null,
        paymentMoney: null,
        shippingCompName: null,
        shippingSn: null,
        shippingTime: null,
        payTime: null,
        receiveTime: null,
        orderStatus: null,
        invoiceTime: null,
        modifiedTime: null
      },
      // 表单参数
      form: {}
    }
  },
  created () {
    this.getList()
  },
  methods: {
    /** 查询订单主列表 */
    getList () {
      this.loading = true
      listMaster(this.queryParams).then((response) => {
        this.masterList = response.rows
        this.total = response.total
        this.loading = false
      })
    },
    // 取消按钮
    cancel () {
      this.open = false
      this.reset()
    },
    // 表单重置
    reset () {
      this.form = {
        orderId: null,
        orderSn: null,
        customerId: null,
        shippingUser: null,
        province: null,
        city: null,
        district: null,
        address: null,
        orderMoney: null,
        districtMoney: null,
        shippingMoney: null,
        paymentMoney: null,
        shippingCompName: null,
        shippingSn: null,
        createTime: null,
        shippingTime: null,
        payTime: null,
        receiveTime: null,
        orderStatus: 0,
        invoiceTime: null,
        modifiedTime: null
      }
      this.resetForm('form')
    },
    /** 搜索按钮操作 */
    handleQuery () {
      this.queryParams.pageNum = 1
      this.getList()
    },
    /** 重置按钮操作 */
    resetQuery () {
      this.resetForm('queryForm')
      this.handleQuery()
    },
    // 多选框选中数据
    handleSelectionChange (selection) {
      this.ids = selection.map((item) => item.orderId)
      this.single = selection.length !== 1
      this.multiple = !selection.length
    },
    /** 新增按钮操作 */
    handleAdd () {
      this.reset()
      this.open = true
      this.title = '添加订单主'
    },
    /** 修改按钮操作 */
    handleUpdate (row) {
      this.reset()
      const orderId = row.orderId || this.ids
      this.$modal
        .confirm('是否确认发货订单主编号为"' + orderId + '"的订单？')
        .then(function () {
          return delMaster(orderId)
        })
        .then(() => {
          this.getList()
          this.$modal.msgSuccess('发货成功')
        })
        .catch(() => {})
    }
    }
}
</script>
